.widget {
  width: 447px;
  height: 582px;
  background: rgba(47, 50, 65, 0.5);
  backdrop-filter: blur(13px);
  border-radius: 20px;
  @include mobile-small {
    width: 100%;
  }

    .toggle {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 32px 0 0;
      margin: 0 0 48px;
        &__button {
          padding: 0;
          margin: 0;
          width: 50%;
          text-align: center;
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 1.35;
          color: #5D6588;
          cursor: pointer;
            &:first-of-type {
              border-right: 1px solid #34384C;
            ;
            }
            &.active {
              color: #246CF9;
            }
        }
    }
    &__info {
      width: calc(100% - 64px);
      margin: 0 auto 35px;
      p {
        margin: 0 0 8px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 1.5;
        color: #fff;
      }
      .amount {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 1.5;
        color: #5d6588;
        margin: 0;
        padding: 0;
          span {
            font-style: normal;
            font-weight: 600;
            font-size: 25px;
            line-height: 1.08;
            color: #fff;
          }
      }
    }
    &__params {
      width: calc(100% - 64px);
      margin: 0 auto 32px;
        .param-container {
          display: flex;
          align-items: center;
          border-bottom: 1px solid #34384C;
          padding: 16px 0;
          &:first-of-type {
            margin-bottom: 24px;
          }
            input {
              padding: 0 0;
              width: 70%;
              background: none;
              border-radius: 0;
              border: none;
              font-style: normal;
              font-weight: normal;
              font-size: 18px;
              line-height: 1.5;
              color: #fff;
              &:focus {
                outline: none;
              }
            }
            .dropdown-container {
              min-width: fit-content;
              width: 30%;
              .dropdown {
                width: 100%;
                &.show {
                  .dropdown-toggle:after {
                    transform: rotate(180deg);
                  }
                }
                .dropdown-toggle {
                  width: 100%;
                  background: none;
                  border-radius: 0 !important;
                  border: none;
                  padding: 0 0 0 16px;
                  display: flex;
                  justify-content: flex-end;
                  align-items: center;
                  font-style: normal;
                  font-weight: 275;
                  font-size: 18px;
                  line-height: 1.6;
                  color: #fff;
                  border-left: 1px solid #34384C;
                  &:focus {
                    box-shadow: none;
                  }
                  &:after {
                    margin-left: 11px;
                    transition: all linear .2s;
                  }
                }
              }
            }
        }
    }
    &__buy {
      padding: 18px 12px;
      display: block;
      margin: 0 auto 24px;
      width: calc(100% - 64px);
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 1.6;
      color: #fff;
    }
    .we-accept {
      text-align: center;
      margin: 0 auto 12px;
    }
    &__footer {
      padding: 0;
      margin: 0 auto;
      width: 100%;
      max-width: 275px;
      display: flex;
      justify-content: space-between;
      align-items: center;
        li {
          width: 43px;
          height: 42px;
          &.mc {
            background: url("../../../assets/images/mc-icon.svg") center no-repeat;
          }
          &.visa {
            background: url("../../../assets/images/visa-icon.svg") center no-repeat;
          }
          &.ap {
            background: url("../../../assets/images/apple-pay-icon.svg") center no-repeat;
          }
          &.gp {
            background: url("../../../assets/images/google-pay-icon.svg") center no-repeat;
          }
          &.pp {
            background: url("../../../assets/images/pp-icon.svg") center no-repeat;
          }
        }
    }
}
.dropdown-menu.show {
  background: #5d6588;
  width: 113px;
  min-width: unset;
    .dropdown-item {
      width: 100%;
      background: none;
      border-radius: 0;
      border: none;
      padding: 8px 12px;
      margin: 0;
      text-align: center;
      font-family: $font-family-graphik;
      font-style: normal;
      font-weight: 275;
      font-size: 18px;
      line-height: 1.6;
      color: #fff;
      display: flex;
      align-items: center;
    }
}
