.support {
  margin: 105px auto 158px;
  width: 100%;
  display: flex;
  position: relative;
  z-index: 10;
  background: url("../../../../assets/images/bg-support.png") no-repeat calc(50% + 310px) 50%;
  @include tablet {
    background-size: 700px auto;
    background-position: calc(50% + 380px) 50%;
  }
  @include mobile {
    flex-direction: column;
    padding-bottom: calc(80vw);
    background-size: 80vw auto;
    background-position: 60% 100%;
  }
  @include mobile-small {
    margin: 45px auto 117px;
  }

    &__title {
      font-size: 61px;
      margin: 0 0 81px;
      @include mobile-small {
        font-size: 34px;
        margin-bottom: 42px;
      }
    }

    &__form {
      margin: 0 auto;
      width: calc(100% - 96px);
      max-width: 1110px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      flex-wrap: nowrap;

      @include mobile {
        align-items: center;
      }
      @include mobile-small {
        width: calc(100% - 48px);
      }

        form {
          width: 100%;

          max-width: 470px;
            .form-group {
              margin: 0 0 23px;
              width: 100%;
              display: flex;
              flex-direction: column;
                input,
                textarea {
                  width: 100%;
                  padding: 21px 32px;
                  background: rgba(47, 50, 65, 0.5);
                  border: 1px solid #34384C;
                  box-sizing: border-box;
                  border-radius: 100px;
                  font-family: $font-family-graphik;
                  font-style: normal;
                  font-weight: normal;
                  font-size: 16px;
                  line-height: 1.69;
                  color: #E5E6ED;
                    &:focus {
                      outline: none;
                    }
                    &.is-invalid {
                      border: 1px solid darkred;
                    }
                    &::placeholder {
                      font-family: $font-family-graphik;
                      font-style: normal;
                      font-weight: normal;
                      font-size: 16px;
                      line-height: 1.5;
                      color: #B9BDEA;
                    }
                }
                textarea {
                  height: 165px;
                  border-radius: 50px;
                  resize: none;
                }
                .invalid-feedback {
                  display: inline-block;
                  padding: 0;
                  margin: 12px 0 0 40px;
                  &:before {
                    display: inline;
                    content: "⚠ ";
                  }
                }
            }
            .btn {
              padding: 16px 32px;
              min-width: fit-content;
              width: 50%;
              font-family: $font-family-graphik;
              font-style: normal;
              font-weight: 600;
              font-size: 16px;
              line-height: 1.6;
              color: #fff;
              @include mobile-small {
                display: block;
                margin: 0 auto;
              }
            }
        }

    }

}
