@import "bloglist";

.blog {
  margin: 105px auto 198px;
  width: calc(100% - 96px);
  max-width: 1110px;
  display: flex;
  flex-direction: column;
  align-items: center;
    @include mobile {
      margin: 45px auto 138px;
      width: calc(100% - 48px);
    }
    &__title {
      font-size: 61px;
      text-align: center;
      margin: 0 auto 32px;
      @include mobile {
        font-size: 45px;
      }
      @include mobile-small {
        font-size: 34px;
        margin-bottom: 19px;
      }
    }
    &__subtitle {
      width: 100%;
      max-width: 713px;
      margin: 0 auto 66px;
      text-align: center;
      @include mobile-small {
        font-size: 16px;
        margin-bottom: 49px;
      }
    }
}
