.news {
  margin-top: 160px;
  position: relative;

  @include tablet {
    margin-top: 73px;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: flex-start;
  }

    &__info {
      max-width: 539px;
      @include tablet {
        max-width: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
      }

        .news__title {
          margin: 0 0 40px;

          @include tablet {
            margin: 0 auto 430px;
            text-align: center;
          }
          @include mobile {
            font-size: 28px;
            margin: 0 auto 320px;
          }

        }
        .new-text {
          margin: 0 0 16px;
          max-width: 480px;
          font-style: normal;
          font-weight: normal;
          font-size: 23px;
          line-height: 1.17;
          color: #e5e6ed;

          @include tablet {
            margin: 0 auto 20px;
            max-width: 400px;
            text-align: center;
          }
          @include mobile {
            max-width: 326px;
            font-size: 20px;
          }
        }
        .new-source {
          margin-bottom: 40px;
          font-style: normal;
          font-weight: 600;
          font-size: 18px;
          line-height: 1.5;
          color: #a5adcf;
          @include tablet {
            margin-bottom: 0;
            position: absolute;
            bottom: 0;
          }
          @include mobile {
            font-size: 16px;
            bottom: 3px;
          }
        }
    }
    &__nav {
      @include tablet {
        display: flex;
        justify-content: space-between;
        width: 280px;
      }
      @include mobile {
        width: 250px;
      }
      button {
        margin: 0;
        padding: 0;
        height: 32px;
        width: 32px;
          &.prev {
            background: url("../../../../assets/images/arrow-prev.svg") center no-repeat;
            margin-right: 28px;
          }
          &.next {
            background: url("../../../../assets/images/arrow-next.svg") center no-repeat;
          }
      }
    }
    &__switcher {
      width: 512px;
      height: 380px;
      list-style: none;
      margin: 0;
      padding: 0;
      position: relative;
      @include tablet {
        position: absolute;
        top: 120px;
      }
      @include mobile {
        position: absolute;
        top: 120px;
        width: 328px;
        height: 243px;
      }
        .source {
          cursor: pointer;
          position: absolute;
          z-index: 10;
          border-radius: 50%;
          background: rgba(47, 50, 65, 0.5);
          transition: all ease-in-out .3s;
          display: flex;
          align-items: center;
          justify-content: space-around;
          overflow: hidden;
          img {
            display: block;
              object-position: center;
              object-fit: contain;
              width: 90%;
              height: auto;
            }

            &:hover {
              filter: drop-shadow(0px 34px 5px rgba(16, 16, 16, 0.1));
              transform: scale(1.02);
            }
            &.first {
              top: calc(50% - 123px);
              left: 0;
              width: 245px;
              height: 245px;
              background: rgba(47, 50, 65, 0.5);
              @include mobile {
                width: 156px;
                height: 156px;
                top: calc(50% - 78px);
                background-size: 122px auto;
              }
            }
            &.second {
              top: 0;
              left: calc(50% - 45px);
              width: 111px;
              height: 111px;
              background: rgba(47, 50, 65, 0.5);
              @include mobile {
                width: 71px;
                height: 71px;
                left: calc(50% - 35px);
                background-size: 52px auto;
              }
            }
            &.third {
              bottom: 20px;
              left: 50%;
              width: 93px;
              height: 93px;
              background: rgba(47, 50, 65, 0.5);
              @include mobile {
                width: 60px;
                height: 60px;
                background-size: 46px auto;
              }
            }
            &.fourth {
              top: calc(50% - 93px);
              right: 0;
              width: 185px;
              height: 185px;
              background: rgba(47, 50, 65, 0.5);
              @include mobile {
                width: 118px;
                height: 118px;
                top: calc(50% - 59px);
                background-size: 92px auto;
              }

            }
            &.active {
              filter: drop-shadow(0px 34px 14px rgba(16, 16, 16, 0.85));
            }
        }
    }
}
