.one-click-exchange {
    position: relative;
    @include mobile {
        margin-top: 82px;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }

    .ellipsis {
        position: absolute;
        height: 338px;
        width: 152px;
        background: url("../../../../assets/images/bg/ellipse.png") center no-repeat;
        background-size: contain;
        z-index: 5;

        left: -185px;
        @include desktop {
            top: 30%;
            left:-100px;
        }
        @include tablet {
            top: 40%;
            left: -60px;
        }
        @include mobile {
            top: 14%;
        }
    }
    .info {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;

        @include mobile {
            align-items: center;
            width: 100%;
        }
            &__title {
                margin: 0 0 24px;
                max-width: 530px;
                @include tablet {
                    max-width: 330px;
                }
                @include mobile {
                    font-size: 28px;
                    text-align: center;
                    max-width: 300px;
                    margin: 0 auto 16px;
                }
            }
            &__description {
                max-width: 500px;
                margin: 0 0 40px;
                @include mobile {
                    text-align: center;
                    max-width: 327px;
                    margin: 0 auto 24px;
                }
            }

            &__list {
                list-style: none;
                padding: 0 0 0 38px;
                display: flex;
                flex-direction: column;

                @include desktop {
                    padding: 0 0 0 12px;
                }
                @include tablet {
                    padding: 0;
                }
                @include mobile {
                    width: 100%;
                    max-width: 447px;
                    margin: 0 auto 40px;
                }
                @include mobile-small {
                    max-width: 100%;
                }
                    .info__item {
                        display: flex;
                        align-items: center;
                        margin-bottom: 32px;
                            .img-container {
                                height: 91px;
                                width: 91px;
                                position: relative;
                                z-index: 10;
                                background: #34384C;
                                backdrop-filter: blur(14px);
                                border-radius: 50%;
                                flex-shrink: 0;

                                @include mobile {
                                    width: 64px;
                                    height: 64px;
                                }
                                img {
                                    display: block;
                                    height: 44px;
                                    width: 44px;
                                    margin: 23px auto 0;
                                    @include mobile {
                                        margin: 8px auto 0;
                                    }
                                }
                            }
                            p {
                                margin: 0 0 0 32px;
                                font-style: normal;
                                font-weight: 600;
                                font-size: 25px;
                                line-height: 1.2;
                                color: #fff;
                                position: relative;
                                z-index: 10;
                                @include tablet {
                                    margin: 0 0 0 16px;
                                    font-size: 18px;
                                }
                                @include mobile {
                                    margin-left: 24px;
                                }
                            }
                            &.second {
                                margin-left: 61px;
                                @media screen and (max-width: 1160px) {
                                    margin-left: 31px;
                                }
                                @include tablet {
                                    margin-left: 0;
                                }
                            }
                            &.third {
                                margin-left: 132px;
                                @media screen and (max-width: 1160px) {
                                    margin-left: 122px;
                                }
                                @include tablet {
                                    margin-left: 0;
                                }
                            }
                    }
            }
    }
}
