.fees {
  margin: 96px auto 194px;
  width: 100%;
  display: flex;
  position: relative;
  z-index: 10;
  background: url("../../../../assets/images/bg-fees.png") no-repeat calc(50% + 310px) 50%;
  @include tablet {
    background-size: 700px auto;
    background-position: calc(50% + 380px) 50%;
  }
  @include mobile {
    background: none;
  }
  @include mobile-small {
    margin: 45px auto 117px;
  }

  &__title {
      font-size: 61px;
      margin: 0 0 48px;
        @include mobile {
          margin: 0 auto 58px;
        }
        @include mobile-small {
          font-size: 34px;
        }
    }

    &__info {
      margin: 0 auto;
      width: calc(100% - 96px);
      max-width: 1110px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      flex-wrap: nowrap;

        @include mobile {
          align-items: center;
        }
        @include mobile-small {
          width: calc(100% - 48px);
        }

        .block-one {
          width: 487px;
          margin: 0 0 19px;
          padding: 45px 44px;
          background: rgba(47, 50, 65, 0.5);
          backdrop-filter: blur(4px);
          border-radius: 12px;

            @include mobile-small {
              width: 100%;
              margin-bottom: 33px;
              padding: 32px 24px 23px;
            }

            &__title {
              width: 100%;
              font-family: $font-family-graphik;
              font-style: normal;
              font-weight: 600;
              font-size: 20px;
              line-height: 1.35;
              color: #e5e6ed;
            }
            .list {
              width: 100%;
              margin: 0;
              padding-left: 0;
                &__item {
                  font-family: $font-family-graphik;
                  font-style: normal;
                  font-weight: normal;
                  font-size: 18px;
                  line-height: 1.94;
                  color: #e5e6ed;
                  margin-left: 21px;
                }
            }
        }
        .block-two {
          width: 487px;
          padding: 40px 44px;
          background: rgba(47, 50, 65, 0.5);
          backdrop-filter: blur(4px);
          border-radius: 12px;

            @include mobile-small {
              width: 100%;
              padding: 28px 24px 36px;
            }

            &__title {
              width: 100%;
              margin: 0;
              padding: 0;
              font-family: $font-family-graphik;
              font-style: normal;
              font-weight: 600;
              font-size: 18px;
              line-height: 1.94;
              color: #e5e6ed;
            }
            &__text {
              width: 100%;
              margin: 0;
              padding: 0;
              line-height: 1.94;

                &.mb {
                  margin-bottom: 24px;
                }
            }
        }
    }
}
