.footer {
  width: calc(100% - 64px);
  max-width: 1110px;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  @include mobile-small {
    width: calc(100% - 32px);
  }

    .footer-title {
      font-family: $font-family-graphik;
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 1.2;
      color: #fff;
      margin: 0 0 24px;
      @include mobile-small {
        font-size: 16px;
        margin: 0 0 16px;
      }
    }

    &__top-block {
      width: 100%;
      margin-bottom: 40px;

      display: flex;
      justify-content: space-between;
      @include tablet {
        flex-wrap: wrap;
        .top-block__item {
          width: 46%;
          margin-bottom: 54px;
          &:last-of-type {
            margin-bottom: 0;
            @include mobile {
              width: 100%;
            }

          }
          @include mobile-small {
            margin-bottom: 0;
          }

        }
      }
      @include mobile-small {
        flex-direction: column;
        flex-wrap: nowrap;
        .top-block__item {
          width: 100%;
        }
      }

        .footer__brand-block {
          max-width: 293px;
          @include mobile-small {
            margin-bottom: 54px;
          }

            .logo {
              width: 183px;
              height: 30px;
              margin: 0 0;
              padding: 0;
              position: relative;
              z-index: 25;
                img {
                  width: 183px;
                  height: 30px;
                }
            }
            .moto {
              margin: 19px 0 22px;
              @include mobile-small {
                font-size: 16px;
              }
            }
            .address {
              font-style: normal;
              font-weight: normal;
              font-size: 16px;
              line-height: 1.5;
              color: #a5adcf;
              @include tablet {
                margin-bottom: 36px;
              }
            }
            .social-links {
              cursor: pointer;
              list-style: none;
              margin: 0;
              padding: 0;
              display: flex;
              @include mobile {}
              justify-content: space-between;

                .social-link {
                  margin: 0 46px 0 0;
                  width: 24px;
                  height: 24px;
                  background-position: center;
                  background-repeat: no-repeat;
                  @include mobile {
                    margin: 0;
                  }
                    &.telegram {
                      background-image: url("../../assets/images/telegram-logo.svg");
                    }
                    &.youtube {
                      background-image: url("../../assets/images/youtube-logo.svg");
                    }
                    &.facebook {
                      background-image: url("../../assets/images/facebook-logo.svg");
                    }
                    &.twitter {
                      background-image: url("../../assets/images/twitter-logo.svg");
                    }
                }
            }
        }

        .footer__links-block {
          @include mobile-small {
            margin-bottom: 40px;
          }
          .links {
            list-style: none;
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;

              .link {

                margin: 0 0 24px;
                a {
                  font-family: $font-family-graphik;
                  font-style: normal;
                  font-weight: normal;
                  font-size: 16px;
                  line-height: 1.5;
                  color: #a5adcf;
                  text-decoration: none;
                  @include mobile-small {
                    font-size: 14px;
                  }
                  }
                @include mobile-small {
                  margin: 0 0 16px;
                  &:last-of-type {
                    margin: 0 0;
                  }
                }
              }
          }
        }
        .footer__subscribe-block {
          max-width: 354px;
          min-width: 327px;
          @include mobile-small {
            max-width: 100%;
            min-width: unset;
          }

            .subscribe-description {
              font-family: $font-family-graphik;
              font-style: normal;
              font-weight: normal;
              font-size: 16px;
              line-height: 1.5;
              color: #a5adcf;

              margin: 0 0 24px;
              @include mobile-small {
                font-size: 14px;
                margin: 0 0 16px;
              }
            }
            .subscribe-form {
              width: 100%;
              max-width: 327px;

                input {
                  height: 63px;
                  @include mobile-small {
                    height: 55px;
                  }
                }
                button {
                  top: 8px;
                  min-width: 129px;
                  height: 47px;
                  @include mobile-small {
                    min-width: 113px;
                    height: 39px;
                    padding: 8px 16px;
                  }
                }
            }
        }
    }

    &__bottom-block {
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding: 32px 0 32px;
      border-top: 1px solid #34384c;
      @include tablet {
        flex-direction: column-reverse;
        justify-content: flex-start;
        align-items: center;
      }
      @include mobile {
        padding: 28px 0 25px;
      }

        p {
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 1.5;
          text-align: right;
          color: #5d6588;
          @include mobile {
            font-size: 14px;
            text-align: center;
          }
        }
        .copyright {
          @include mobile {
            p {
              margin-bottom: 0;
            }
          }
        }
        .politics {
          display: flex;
          justify-content: flex-end;
          @include mobile {
            flex-direction: column;
            justify-content: flex-start;
            margin-bottom: 8px;
          }

            &-item {
              text-decoration: none;
              font-family: $font-family-graphik;
              font-style: normal;
              font-weight: normal;
              font-size: 16px;
              line-height: 1.5;
              color: #5D6588;
              cursor: pointer;
              &:last-of-type {
                padding-left: 20px;
                border-left: 1px solid #34384c;
              }
              &:first-of-type {
                padding-right: 20px;
              }
              @include mobile {
                margin-bottom: 8px;
                &:last-of-type {
                  padding-left: 0;
                  border-left: none;
                }
                &:first-of-type {
                  padding-right: 0;
                }
              }
            }
        }
    }
}
