@import "faqlist";

.faq {
  margin: 105px auto 173px;
  width: calc(100% - 96px);
  max-width: 1110px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @include mobile {
    margin: 45px auto 175px;
    width: calc(100% - 48px);
  }
  &__title {
    font-size: 61px;
    text-align: left;
    margin: 0 0 81px;
    @include mobile {
      font-size: 45px;
    }
    @include mobile-small {
      font-size: 34px;
      margin-bottom: 56px;
    }
  }
}
