.howdoesitwork {
  margin: 105px auto 184px;
  width: calc(100% - 96px);
  max-width: 1110px;
  display: flex;
  flex-direction: column;

    b {
      color: #a5adcf;
    }
    a {
      color: #a5adcf;
      text-decoration: none;
      font-weight: bolder;
    }
    @include mobile {
      margin: 45px auto 189px;
      width: calc(100% - 48px);
    }

    &__title {
      font-size: 61px;
      margin: 0 0 76px;
      @include mobile {
        margin: 0 auto 52px;
        font-size: 45px;
      }
      @include mobile-small {
        font-size: 34px;
      }
    }

    .item {
      display: flex;
      width: 100%;
      padding: 38px 42px 37px;
      margin: 0 auto 56px;
      background: rgba(47, 50, 65, 0.5);
      backdrop-filter: blur(4px);
      border-radius: 12px;
      position: relative;
        @include mobile {
          flex-direction: column;
          width: 100%;
          padding-bottom: 256px;
          margin-bottom: 133px;
        }
        @include mobile-small {
          padding: 25px 24px 256px;
        }
        &:last-of-type {
          margin-bottom: 0;
        }
        &__info {
          max-width: 56%;
          @include mobile {
            max-width: 100%;
          }
        }
        &__image {
          position: absolute;
            &.one {
              width: 512px;
              height: 512px;
              background: url("../../../../assets/images/moneybag.png") center no-repeat;
              background-size: contain;
              top: -110px;
              right: -50px;
              @include tablet {
                height: 450px;
                width: 450px;
                top: calc(50% - 225px);
              }
              @include mobile {
                width: 100vw;
                height: 100vw;
                max-width: 404px;
                max-height: 404px;
                top: unset;
                bottom: -150px;
                right: 0;
                left: calc(50% - 202px)
              }
              @include mobile-small {
                max-width: unset;
                max-height: unset;
                left: calc(50% - 50vw);
                bottom: -40vw;
              }
            }
            &.two {
              width: 579px;
              height: 579px;
              background: url("../../../../assets/images/shopping-cart.png") center no-repeat;
              background-size: contain;
              top: -90px;
              right: -90px;
              @include tablet {
                width: 470px;
                height: 470px;
                top: calc(50% - 235px);
              }
              @include mobile {
                width: 100vw;
                height: 100vw;
                max-width: 420px;
                max-height: 420px;
                top: unset;
                bottom: -100px;
                right: 0;
                left: calc(50% - 202px)
              }
              @include mobile-small {
                max-width: unset;
                max-height: unset;
                left: calc(50% - 50vw);
                bottom: -25vw;
              }
            }
            &.three {
              width: 460px;
              height: 460px;
              background: url("../../../../assets/images/pitch.png") center no-repeat;
              background-size: contain;
              top: 60px;
              right: -43px;
              @include tablet {
                width: 400px;
                height: 400px;
                top: calc(50% - 200px);
              }
              @include mobile {
                width: 90vw;
                height: 90vw;
                max-width: 327px;
                max-height: 327px;
                top: unset;
                bottom: -100px;
                right: 0;
                left: calc(50% - 163px)
              }
              @include mobile-small {
                max-width: 75vw;
                max-height: 75vw;
                left: calc(50% - 35vw);
                bottom: -15vw;
              }
              @media screen and (max-width: 400px) {
                bottom: -5vw;
              }
            }
        }
        &__title {
          margin: 0 0 21px;
          font-family: $font-family-graphik;
          font-style: normal;
          font-weight: 600;
          font-size: 24px;
          line-height: 1.12;
          color: #e5e6ed;
          @include mobile {
            margin-bottom: 24px;
          }
        }
        .list {
          padding: 0;
          margin: 0;
            &__item {
              margin-left: 21px;
              margin-bottom: 12px;
              font-family: $font-family-graphik;
              font-style: normal;
              font-weight: normal;
              font-size: 18px;
              line-height: 1.5;
              color: #e5e6ed;
            }
        }
    }
}
