.video {
  margin-top: 0;
  justify-content: flex-start;
  position: relative;
  background: url("../../../../assets/images/bg/top-grid.svg") center no-repeat;
  background-size: cover;
  padding: 96px 0;

  @include tablet {
    padding-bottom: 155px;
    padding-top: 57px;
    flex-direction: column-reverse;
    align-items: center;
  }

    .line-bottom-right {
      position: absolute;
      height: 702px;
      width: 448px;
      background: url("../../../../assets/images/bg/line-bottom-right.svg") center no-repeat;
      background-size: contain;
      z-index: 5;

      top: -200px;
      right: -170px;

      @include tablet {
        top: -400px;
        right: -250px;
      }
      @include mobile {
        transform: scaleY(.4);
        top: -200px;
        right: -170px;
      }
      @include mobile-small {
        top: -280px;
      }
    }

    &__video-block {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 549px;
      height: 668px;
      background: url("../../../../assets/images/video-bg.svg") center no-repeat;
      transition: all ease-in-out .3s;
      @include tablet {
        justify-content: space-around;
        height: unset;
        width: 100%;
        background: unset;
      }
        .video__blank {
          height: 260px;
          width: 490px;
          background: #1E1F25 url("../../../../assets/images/play-icon.svg") center no-repeat;
          backdrop-filter: blur(2.01078px);
          filter: drop-shadow(0px 34px 14px rgba(16, 16, 16, 0.85));
          border-radius: 1px;
          border-left: 5px solid #F7931A;
          cursor: pointer;
          position: relative;
          z-index: 10;
          @include tablet {
            width: 100%;
            max-width: 549px;
          }
          @include mobile-small {
            height: 180px;
          }

          &:hover {
            transform: scale(1.01);
          }
        }
    }

    &__info {
      max-width: 484px;
      margin-left: 56px;
      @include tablet {
        margin: 0 auto 46px;
        width: 100%;
        max-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
      }
      @include mobile-small {
        max-width: 327px;
      }

      .video__title {
          width: 380px;
          margin: 0 0 24px;
        position: relative;
        z-index: 10;
          @include tablet {
            width: 100%;
            max-width: 500px;
            text-align: center;
            margin: 0 auto 16px;
          }
          @include mobile-small {
            font-size: 28px;
          }
        }
        .video__description {
          max-width: 380px;
          position: relative;
          z-index: 10;
          @include tablet {
            text-align: center;
            width: 100%;
            max-width: 500px;
            margin-bottom: 0;
          }
          @include mobile-small {
            font-size: 14px;
          }
        }
    }
}
