.faq__list {
  width: 100%;
  list-style: none;
  margin: 0;
  padding: 0;

    li {
      padding: 28px 32px;
      background: rgba(47, 50, 65, 0.5);
      border: 1px solid #34384C;
      box-sizing: border-box;
      border-radius: 50px;
      margin: 0 0 23px;
      position: relative;
      @include mobile-small {
        padding: 36px 31px;
      }
      &:last-of-type {
          margin-bottom: 0;
        }
        p {
          color: #b9bdea;
        }
        .question {
          cursor: pointer;
          font-weight: 600;
          margin: 0;
          padding: 0;
        }
        .answer {
          margin: 32px 0 0;
          width: 100%;
          display: none;
          transition: all linear .3s;

          p, h1, h2, h3, h4, h5, h6, span, strong {
            color: #e5e6ed !important;
          }
          img {
            max-width: 100%;
          }
          a {
            text-decoration: underline;
            color: #a5adcf !important;
            font-weight: bold;
          }

          @include mobile-small {
            p {
              font-size: 16px;
            }

          }

          ul {
              list-style: none;
              padding-left: 21px;
            }
        }
        .expand {
          display: block;
          width: 20px;
          height: 20px;
          background: url("../../../../assets/images/arrow-down-purple.svg") center no-repeat;
          background-size: contain;
          position: absolute;
          top: 32px;
          right: 29px;
          transition: all linear .3s;
          cursor: pointer;
        }
      &.expanded {
        .answer {
          display: block;

        }
        .expand {
          transform: rotate(180deg);
        }
      }
    }
}
