.trust {
  @include tablet {
    flex-direction: column-reverse;
  }
  @include mobile {
    margin-top: 52px;
  }
    &__cards {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: space-around;

      height: 682px;

      @include tablet {
        margin-top: 32px;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        width: 100%;
        height: unset;
      }
      @include mobile {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
      }

        .card {
          width: 285px;
          height: 329px;
          padding: 40px 28px 16px;

          background: rgba(47, 50, 65, 0.5);
          backdrop-filter: blur(4px);
          border-radius: 12px;

          @include desktop {
            padding: 40px 14px 16px;
            width: 257px;
          }
          @include tablet {
            width: 30%;
          }
          @include mobile {
            height: 297px;
            padding: 32px 28px 16px;
            width: 100%;
            max-width: 440px;
            margin-bottom: 24px;
          }
          @include mobile-small {
            max-width: 285px;
            margin-bottom: 16px;
          }
          @include mobile-micro {
            height: unset;
          }
            &:first-of-type {
              margin-bottom: 24px;
              @include tablet {
                margin-bottom: 0;
              }
              @include mobile {
                margin-bottom: 24px;
              }
              @include mobile-small {
                margin-bottom: 16px;
              }
            }
            &:last-of-type {
              margin-left: 24px;
              @include tablet {
                margin-left: 0;
              }
              @include mobile {
                margin-bottom: 0;
              }
            }

            .img-wrapper {
              width: 91px;
              height: 91px;
              background: #34384C;
              backdrop-filter: blur(14px);
              border-radius: 50%;

              margin: 0 auto;
                img {
                  display: block;
                  margin: 23px auto 0;
                  width: 44px;
                  height: 44px;
                }
            }

            &__title {
              margin: 40px auto 0;
              font-style: normal;
              font-weight: 700;
              font-size: 25px;
              line-height: 1.2;
              text-align: center;
              color: #fff;
              @include mobile-small {
                margin-top: 32px;
                font-size: 18px;
              }
            }

            &__description {
              margin: 16px auto 0;
              font-style: normal;
              font-weight: normal;
              font-size: 16px;
              line-height: 1.5;
              text-align: center;
              color: #a5adcf;
              @include mobile-small {
                font-size: 14px;
              }
            }
        }
    }

    .info {
      width: 50%;
      max-width: 448px;

      @include desktop {
        max-width: 360px;
      }
      @include tablet {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        width: 100%;
        max-width: 100%;
      }

      &__title {
          max-width: 350px;
          margin: 0 0 24px;
        position: relative;
        z-index: 10;
          @include tablet {
            max-width: 100%;
            text-align: center;
            margin: 0 auto 16px;
          }
        @include mobile {
          font-size: 28px;
        }
        }

        &__description {
          width: 100%;
          max-width: 430px;
          margin: 0 0 32px;
          position: relative;
          z-index: 10;
          @include tablet {
            max-width: 600px;
            text-align: center;
            margin: 0 auto 27px;
          }
          @include mobile-small {
            font-size: 14px;
          }
        }

        &__btn {
          padding-top: 16px;
          padding-bottom: 16px;
          min-width: 225px;
          position: relative;
          z-index: 10;
          @include mobile-small {
            padding-top: 12px;
            padding-bottom: 12px;
            font-size: 14px;
          }
        }
    }
}
