@import "./assets/fonts.css";
@import "./assets/scss/all.scss";

.App {
  background: #1E1F25;
  position: relative;
  width: 100vw;
  overflow-x: hidden;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }
    input[type='number'] {
      -moz-appearance: textfield;
    }


  .main-wrapper {
      min-height: 65vh;
    }

    .bg-grid-top {
      background: url("./assets/images/bg/top-grid.svg") center no-repeat;
      background-size: cover;
    }
    .bg-top-line {
      height: 850px;
      width: 100vw;
      max-width: 1440px;
      position: absolute;
      top: -100px;
      left: 0;
      z-index: 5;
      background: url("./assets/images/bg/top-line.svg") center no-repeat;
      background-size: 100% auto;
      @media screen and (min-width: 1440px) {
        left: calc((100% - 1440px) / 2)
      }
      @include tablet {
        top: -80px;
      }
      @include mobile {
        top: -250px;
        width: 100%;
        height: 500px;
        background-position: top left;
        background-size: 150% 100%;
      }
      @media screen and (max-width: 640px) {
        transform: rotate(-5deg);
        height: 300px;
        top: -50px;
        background-size: 140% 100%;
      }
      @include mobile-small {
        top: -60px;
      }
      @include mobile-micro {
        top: -50px
      }
    }
    .wave-top-right {
      height: 223px;
      width: 787px;
      position: absolute;
      top: 1320px;
      right: -160px;
      z-index: 5;
      background: url("./assets/images/bg/wave-top-right.svg") center no-repeat;
      background-size: contain;
      @media screen and (min-width: 1440px) {
        right: calc(((100vw - 1440px) / 2) - 160px)
      }
      @include tablet {
        top: 1100px;
        right: -70px;
        width: 487px;
        height: 140px;

      }
      @include mobile {
        top: 770px;
        right: -90px;
        width: 387px;
        height: 110px;
      }
      @include mobile-small {
        top: 740px;
      }
      @include mobile-micro {
        top: 770px;
      }
    }
    .loop {
      position: absolute;
      height: 280px;
      width: 555px;
      background: url("./assets/images/bg/loop.svg") center no-repeat;
      background-size: contain;
      z-index: 5;

      bottom: 320px;
      left: 0;
      @media screen and (min-width: 1440px) {
        left: calc(((100vw - 1440px) / 2))
      }
      @include desktop {
        left: -100px;
      }
      @include tablet {
        bottom: 750px;
      }
      @include mobile {
        bottom: 780px;
        left: -200px;
        transform: scaleY(.7);
      }
      @include mobile-small {
        left: -210px;
        bottom: 930px;
      }
    }
    &.radial-gradient {
      width: 100%;
      background: radial-gradient(40% 40% at 50% 25%, rgba(1, 9, 80, 0.9) 3.12%, #1E1F25 100%);
      @include mobile {
        background: radial-gradient(40% 15% at 50% 18%, rgba(1, 9, 80, 0.9) 3.12%, #1E1F25 100%);
      }
    }
}
