.blog-post {
  width: calc(100% - 96px);
  max-width: 1110px;
  margin: 105px auto 136px;
  display: flex;
  flex-direction: column;
  @include mobile {
    width: calc(100% - 48px);
    margin: 45px auto 104px;
  }

    &__title {
      font-size: 36px;
      max-width: 874px;
      margin: 0 0 70px;
        @include mobile {
          font-size: 30px;
          margin-bottom: 37px;
        }
    }
    &__img-container {
      margin: 0 auto 70px;
      width: initial;
      max-width: 100%;
      border-radius: 10px;
      @include mobile {
        margin-bottom: 33px;
      }
        img {
          width: initial;
          max-width: 100%;
          height: auto;
          border-radius: 10px;
        }
    }
    &__info {
      width: 100%;
      margin: 0 0 55px;
      @include mobile {
        p {
          font-size: 16px;
        }
      }
      @include mobile-small {
        margin-bottom: 40px;
      }
    }
    &__date {
      font-family: $font-family-graphik;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 1.5;
      color: #82848e;
    }
}
