.carousel {
  width: 100%;
  max-width: 1440px;
  @include mobile {
    margin-top: 32px;
  }
  @media screen and (max-width: 560px) {
    width: calc(100% - 48px);
  }
  span {
    color: white;
  }
  .swiper-wrapper {
    padding-bottom: 32px;
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
    @media screen and (max-width: 560px) {
      padding-left: 0;
      padding-right: 0;
      .swiper-slide {
        width: 100% !important;
      }
    }
  }
  .swiper-pagination {
    bottom: 0 !important;
    .swiper-pagination-bullet {
      width: 8px;
      height: 8px;
      background: #34384C;
    }
    .swiper-pagination-bullet-active {
      width: 27px;
      border-radius: 20px;
      background: linear-gradient(265.56deg, #246CF9 -0.27%, #1E68F6 -0.26%, #0047D0 98.59%);
    }
  }
}
