.affiliate {
  margin: 105px auto 133px;
  width: calc(100% - 96px);
  max-width: 1110px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 10;

    b {
      color: #a5adcf;
    }
    a {
      color: #a5adcf;
      text-decoration: none;
      font-weight: bolder;
    }
    @include mobile-small {
      margin: 45px auto 117px;
    }

    &__title {
      max-width: 759px;
      font-size: 61px;
      margin: 0 0 53px;
      @include mobile {
        font-size: 50px;
        margin: 0 auto 58px;
      }
      @include mobile-small {
        font-size: 34px;
      }
    }

    .info {
      width: 100%;
      padding: 45px 47px 53px;
      background: rgba(47, 50, 65, 0.5);
      backdrop-filter: blur(4px);
      border-radius: 12px;

      @include mobile {
        padding: 45px 23px 48px;
      }

        .list {
          width: 100%;
          max-width: 597px;
          list-style: none;
          margin: 34px 0;
          padding: 17px 36px 17px 45px;
          background: rgba(23, 25, 32, 0.7);
          backdrop-filter: blur(4px);
          border-radius: 12px;

          @include mobile {
            padding: 17px 12px;
          }

          .item {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 12px 0;
            @include mobile-small {
              padding: 6px 0;
            }
              .info__text {
                padding: 0;
                margin: 0;
                @include mobile-small {
                  font-size: 14px;
                }
              }
              .right {
                display: flex;
                align-items: flex-start;
                margin-left: 12px;
                min-width: 70px;
              }
            }
        }
    }
}
