.slide {
  width: 100%;
  height: 178px;
  padding: 24px;
  z-index: 10;
  background: rgba(47, 50, 65, 0.5);
  backdrop-filter: blur(4px);
  border-radius: 8px;

  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;

    &__top-block {
      width: 100%;
      display: flex;
      justify-content: space-between;

        .currency-icon {
          height: 44px;
          width: 44px;
        }
        .currency-graph {
          height: 44px;
          width: 138px;
        }
    }

    &__diff {
      align-self: flex-end;
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 1.5;
      text-align: right;
      display: flex;
      align-items: center;
        &.down {
          color: #fa2256;
          &:before {
            display: block;
            content: '';
            height: 20px;
            width: 20px;
            background: url("../../../assets/images/diff-arrow-down.svg") center no-repeat;
          }
        }
        &.up {
          color: #11cabe;
          &:before {
            display: block;
            content: '';
            height: 20px;
            width: 20px;
            background: url("../../../assets/images/diff-arrow-up.svg") center no-repeat;
          }
        }
    }

    &__currency {
      margin: 0 0 11px;
      padding: 0;

      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 1.5;
      color: #a5adcf;
    }

    .bottom-block {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: baseline;

        &__price {
          font-style: normal;
          font-weight: 600;
          font-size: 25px;
          line-height: 1.08;
          color: #fff;
        }

        &__key {
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 1.5;
          text-align: right;
          color: #5d6588;
        }
    }
}

// hardcode for Torexo
.slide.torexo {
  .currency-icon {
    background-color: #fee600;
    border-radius: 50%;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: #141414;
    font-size: 22px;
    font-weight: 700;
  }
}
