.bloglist {
  width: 100%;

  margin: 0 auto;
  display: grid;
  grid-gap: 7px;
  grid-template-columns: 36% 36% 26%;
  grid-auto-rows: 206px;
  counter-reset: div;

  @include tablet {
    grid-template-columns: 50% 48%;
  }
  @include mobile {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  @media screen and (max-width: 600px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  .blog-item {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
      border-radius: 12px;
      padding: 26px 25px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      cursor: pointer;
      @include mobile {
        height: 304px;
        width: 49%;
        margin-bottom: 19px;
      }
      @media screen and (max-width: 600px) {
        width: 100%;
        max-width: 500px;
      }
        &:before {
          position: absolute;
          top: -1px;
          left: -1px;
          width: 102%;
          height: 102%;
          content: '';
          background: radial-gradient(70% 70% at 50% 50%, rgba(1, 9, 80, 0.7) 3.12%, #1E1F25 100%);
          backdrop-filter: blur(4px);
          border-radius: 12px;
          z-index: 10;
          transition: all linear .4s;
          @include mobile {
            background: radial-gradient(70% 70% at 50% 50%, rgba(1, 9, 80, 0.5) 3.12%, #1E1F25 100%);
          }
        }
        &:hover {
          &:before {
            opacity: .5;
            backdrop-filter: none;
          }
        }
        .title {
          font-family: $font-family-graphik;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 1.1;
          color: #fff;
          position: relative;
          z-index: 20;
        }
    }
  .blog-item:nth-child(4n + 3),
  .blog-item:nth-child(4n + 1) {
    grid-row: auto /span 2;
    @include mobile {
      grid-row: unset;
    }
  }


}
