@font-face {
    font-family: 'Graphik';
    src: local('Graphik Semi bold'), local('Graphik-Semi-bold'),
    url('fonts/Graphik-Semibold.woff2') format('woff2'),
    url('fonts/Graphik-Semibold.woff') format('woff'),
    url('fonts/Graphik-Semibold.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
}
