.main-info {
  display: flex;
  justify-content: space-between;
  max-width: 1440px;

  margin: 0px auto 0;

    &__info {
      margin-left: 165px;
      margin-top: 96px;
      max-width: 580px;
      width: 50%;

      @include desktop {
        margin-left: 100px;
        width: 40%;
      }
      @include mobile {
        margin: 45px auto 0;
        width: calc(100% - 48px);
        display: flex;
        flex-direction: column;
        align-items: center;
      }
      @include mobile-small {
        width: calc(100% - 36px);
      }

        h1 {
          width: 100%;
          margin-bottom: 33px;
          font-size: 61px;
          line-height: 1.1;
          position: relative;
          z-index: 10;

          @include desktop {
            font-size: 44px;
          }
          @include mobile {
            text-align: center;
            margin-bottom: 24px;
          }
          @include mobile-small {
            font-size: 32px;
          }
        }

        p {
          width: 100%;
          margin-bottom: 66px;
          position: relative;
          z-index: 10;

          @include desktop {
            max-width: 400px;
          }
          @include mobile {
            max-width: 100%;
            text-align: center;
            margin-bottom: 32px;
          }
        }

        button {
          width: 287px;
          padding-top: 18px;
          padding-bottom: 18px;
          position: relative;
          z-index: 10;
          @include mobile {
            margin: 0 auto;
            width: 100%;
            max-width: 400px;
          }
        }
    }
    &__image {
      width: 50%;
      height: 611px;

      @include desktop {
        width: 60%;
      }
      @include mobile {
        display: none;
      }
        img {
          width: 100%;
          height: 100%;
          position: relative;
          z-index: 10;
          @include tablet {
            object-fit: cover;
            object-position: top left;
          }
        }
    }
}
