.main-wrapper {
  width: 100vw;
  overflow-x: hidden;
}
.section {
  max-width: 1110px;
  width: calc(100% - 64px);

  margin: 120px auto 0;

  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
    @include desktop {
      width: calc(100% - 96px);
    }
  @include mobile-small {
    width: calc(100% - 36px);
  }
    &.hidden {
      display: none;
    }
}
