.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  max-width: 1110px;
  width: calc(100% - 48px);
  margin: 0 auto;
  padding: 24px 0 0;
  @include mobile-small {
    width: calc(100% - 32px);
  }

    &__logo {
      height: 30px;
      width: 183px;
      position: relative;
      z-index: 25;
        img {
          width: 183px;
          height: 30px;
        }

      @include mobile-micro {
        min-width: 150px;
        margin-right: 0;
      }
    }

    .nav {
      display: flex;
      justify-content: space-between;
      flex-wrap: nowrap;
      position: relative;
      z-index: 10;
      margin: 0 24px;

      @include mobile {
        width: 100vw;
        margin: 0;
        flex-direction: column;
        align-items: flex-end;
        padding: 100px 24px 24px;
        position: absolute;
        top: -1000px;
        left: -24px;
        background: rgba(47, 50, 65, 1);
        border-radius: 10px;
        z-index: 15;
        transition: all linear .3s;
        li {
          padding: 12px 0;
          a {
            font-size: 18px;
          }
        }
      }

        .links-container {
          display: flex;
          flex-direction: column;
          align-items: flex-end;
          margin-right: 12px;
        }
        &__item {
          min-width: fit-content;
          width: 100%;
          &.ta-right {
            text-align: right;
          }
          @include mobile {
            text-align: right;
          }

            a {
              font-family: $font-family-graphik;
              font-style: normal;
              font-weight: 500;
              font-size: 14px;
              line-height: 1.6;
              color: #fff;

              text-decoration: none;
            }
        }
    }

    &__right-block {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      justify-content: flex-end;
    }
    &__buttons {
      margin-left: 32px;
      display: flex;
      flex-wrap: nowrap;
      min-width: fit-content;

      @include tablet {
        margin-left: 14px;
        position: absolute;
        top: -100px;
        transition: all ease-out .4s;

      }

        .login {
          background: transparent;
          border: 1px solid white;
        }
        .signup {
          margin-left: 19px;
        }
    }

    .mobile-menu {
      margin-left: 40px;
      border-radius: 0;
      display: none;
      width: 24px;
      height: 24px;
      background: transparent url("../../assets/images/menu-icon.svg") no-repeat center;
      cursor: pointer;
      position: relative;
      z-index: 10;
      transition: all linear .1s;

      @include tablet {
        display: block;
        margin-left: 0;
      }
    }

    &.mobile-opened {
      position: relative;
      @include tablet {
        .nav {
          display: none;
        }
        .header__buttons {
          display: flex;
          margin-right: 45px;
          z-index: 15;
          position: relative;
          top: 0;
          transition: all ease-out .4s;

        }
        .mobile-menu {
          background: none;
          z-index: 20;

          &:before {
            display: block;
            content: '';
            position: absolute;
            top: 12px;
            left: 0;
            width: 24px;
            height: 2px;
            background: white;
            transform: rotate(45deg);
          }
          &:after {
            display: block;
            content: '';
            position: absolute;
            top: 12px;
            left: 0;
            width: 24px;
            height: 2px;
            background: white;
            transform: rotate(-45deg);
          }
        }
      }
      @include mobile {
        .nav {
          top: 0;
          display: flex;
        }
        .header__buttons {
          margin: 0 24px 0 6px;
          button {
            font-size: 12px;
            padding: 8px 14px;
          }
        }
        .header__logo {
          //font-size: 16px;
          min-width: fit-content;
        }
      }
      @media screen and(max-width: 615px) {
        flex-direction: column-reverse;
        align-items: flex-start;
        margin: 0 auto;
        width: 100%;

          .header__logo {
            display: none;
          }
          .header__right-block {
            width: 100%;
            padding: 0;
            margin: 0;
          }
          .nav {
            left: 0;
            li {
              margin-right: 0;
            }
          }
        .lang-select {
          display: none !important;
        }
      }
    }
}
