.bonus {
  width: calc(100% - 278px);
  max-width: 1162px;

  position: relative;
  margin: 0 auto;
  padding-bottom: 113px;

  @include tablet {
    width: calc(100% - 96px);
  }
  @include mobile-small {
    padding-bottom: 140px;
    width: calc(100% - 36px);
  }

    &__subtitle {
      margin: 0 auto 16px;
      text-align: center;
      position: relative;
      z-index: 10;
      @include mobile-small {
        font-size: 14px;
        margin-bottom: 22px;
      }
    }
    &__title {
      max-width: 1100px;
      margin: 0 auto 40px;
      text-align: center;
      position: relative;
      z-index: 10;

      @include mobile-small {
        font-size: 28px;

      }
    }

    &__form {
      max-width: 462px;
      margin: 0 auto;
      @include mobile-small {
        width: 100%;
      }
      input {
        height: 67px;
        @include mobile-small {
          height: 55px;
        }
      }
      button {
        min-width: 125px;
        @include mobile-small {
          height: 40px;
          padding: 8px 16px;
          top: 8px;
          min-width: 54px;
        }
      }
    }

}
